import { Pipe, PipeTransform } from '@angular/core';
import {ListModel} from "../../models/list";
import {ListItemModel} from "../../models/listitem";

/**
 * Generated class for the SearchbarFilterPipe pipe.
 *
 * See https://angular.io/api/core/Pipe for more info on Angular Pipes.
 */
@Pipe({
  name: 'searchbarFilter',
})
export class SearchbarFilterPipe implements PipeTransform {
  transform(items: ListModel, filterstr: string) {
    console.log("hello: " + filterstr);
    if (filterstr == null || filterstr == "")
      return items;

    let li: ListItemModel[] = [];
    for (let i in items) {
      let item = items[i];
      if (item.label.startsWith(filterstr))
        li.push(item);
    }

    return new ListModel(li);
  }
}



